import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { login } from "../redux/actions/auth";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";

const mapState = ({ auth }) => ({
	isAuthenticated: auth.isAuthenticated,
});

function Login() {
	const { t } = useTranslation(["account"]);
	// form
	const [formData, setFormData] = useState({
		username: "",
		password: "",
	});
	const [isLoading, setIsLoading] = useState(false);
	const { username, password } = formData;
	const onChange = (e) =>
		setFormData({ ...formData, [e.target.name]: e.target.value });

	// redux
	const dispatch = useDispatch();
	const { isAuthenticated } = useSelector(mapState);

	// submit
	const onSubmit = (e) => {
		e.preventDefault();
		setIsLoading(true);
		dispatch(login(username, password)).then((res) => {
			setIsLoading(false);
		});
	};

	// redirect
	if (isAuthenticated) {
		return <Redirect to="/" />;
	}

	return (
		<div
			className="bg-white pt-4 p-5 mx-4 mx-sm-auto"
			style={{
				borderRadius: "30px",
				maxWidth: "400px",
				margin: "70px auto",
				boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
			}}
		>
			<h2 style={{ fontWeight: 700 }} className="text-center py-3 pb-4">
				{t("account:Welcome")}
			</h2>
			{/* Big Header Start */}
			<form onSubmit={(e) => onSubmit(e)}>
				<input
					type="text"
					className="form-control py-2"
					placeholder={t("account:Username")}
					name="username"
					id="loginUsername"
					value={username}
					style={{ borderRadius: "15px" }}
					onChange={(e) => onChange(e)}
					required
				/>
				<input
					type="password"
					className="form-control my-3 py-2"
					placeholder={t("account:Password")}
					name="password"
					value={password}
					id="loginPassword"
					style={{ borderRadius: "15px" }}
					onChange={(e) => onChange(e)}
					required
					onKeyDown={(e) => {
						if (e.key === "Enter") {
							onSubmit(e);
						}
					}}
				/>
				<div className="row">
					<button
						className="btn btn-primary mx-auto my-4 "
						style={{
							maxWidth: "300px",
							borderRadius: "15px",
							fontSize: "18px",
						}}
						type="submit"
					>
						{isLoading ? (
							<div class="spinner-border text-white" role="status">
								<span class="visually-hidden">Loading...</span>
							</div>
						) : (
							t("account:Login")
						)}
					</button>
				</div>
			</form>
			<p className="mt-4">
				{t("account:Dont have an account?")}{" "}
				<Link
					to="/signup"
					style={{ fontSize: "15px", borderRadius: "10px" }}
					className="badge  btn-primary my-1 ms-3 py-2  text-decoration-none"
				>
					{t("account:Sign up")}{" "}
					<i class="bi bi-arrow-right-circle-fill ms-2 "></i>
				</Link>
			</p>
			<p className="my-0">
				{t("account:Forgot password?")}{" "}
				<Link
					className="badge  btn-primary my-1 ms-3 py-2  text-decoration-none"
					to="/reset-password"
					style={{ fontSize: "15px", borderRadius: "10px" }}
				>
					{t("account:Reset Password")}
					<i class="bi bi-arrow-right-circle-fill ms-2 "></i>
				</Link>
			</p>
		</div>
	);
}

export default Login;
